@charset "utf-8";
.todo_item{
  padding: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;/* 세로 가운데 정렬*/
  cursor: pointer;
  line-height: 40px;
  border-radius: 10px;
}

.todo_item:hover{
  background-color: #ccc;
}

/* todo_item에 마우스가 있을때만 .remove보이기 */
.todo_item:hover .remove{
  opacity:1;
}

/* todo_item 사이에 윗 테두리 */
.todo_item + .todo_item{
  border-top: 1px solid #f1f3f5;
}
/* 삭제 버튼 서식x */
.remove{
  opacity: 0;
  font-weight: bold;
  color: #f00;
  margin-right: 0.5rem;
}
.del_btn{
  width: 100px;
  height: 30px;
  background-color: #f05c52;
  border: none;
  color: #fff;
  border-radius: 3px;
}
.todo_text{
  flex: 1;
  word-break:break-all
}
/* 해당 글 목록 클릭시 글자 서식이 흐리고, 가운데 줄 표시 하도록 함 */
.checked{
  text-decoration: line-through;
  color:#abd5bd;
}
.check_mark{
  font-size: 1.5rem;
  line-height: normal;
  color: #f00;
  font-weight: bold;
}
