@charset "utf-8";
/* 입력 폼 서식 */

.form{
  display: flex;
  padding: 1rem;
}
.form input{
  flex: 1;
  font-size: 1.25rem;
  outline: none;
  border: none;
  border-bottom:2px solid #ccc;
  text-indent: 5px;
}

/* 추가(입력)버튼 서식 */
.create_btn{
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  padding-left: 1rem;
  padding-right: 1rem;
  background-color: #5f5f5f;
  border-radius: 3px;
  color: #fff;
  font-weight: 600;
  border: none;
  cursor: pointer;
}
.create_btn:hover{
  background: #3bc9db;
}